/******************************
COLOR PALETTE
White: rgb(240, 243, 244);

White: rgb(206, 217, 247);
Brown: rgb(242, 197, 138);
Blue: rgb(40, 10, 195);
Lighter Blue: rgb(20, 52, 164);
Red: rgb(225, 61, 103);
Black: rgb(2, 0, 7);
font-family: 'Dosis', sans-serif;
font-family: gyparody, sans-serif;
font weights: 500, 700, 900;
******************************/
:root {
  --white: #FFFFFC;
  --brown: #D9D5C1;
  --main-blue: #347EBF;
  --light-blue: #91CDF2;
  --blue: #6085A6;
  --dark-blue: #16558C;
  --darker-blue: #022859;
  --very-dark-blue: #011126;
  --black: #0D0D0D;
  --red: #D92B04;
  --title-font: "gyparody", sans-serif;
  --body-font: "Dosis", sans-serif;
  --regular-font-weight: 500;
  --bold-font-weight: 700;
  --heavy-font-weight: 900;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
  /* 1rem = 10px ems vh vw */
  margin: 0;
  padding: 0;
  font-weight: bold;
}

*, *:before, *:after {
  box-sizing: inherit;
  margin: inherit;
  padding: inherit;
}

body {
  background-color: var(--white);
  background-image: linear-gradient(to bottom, var(--white) 45%, var(--light-blue) 95%);
}

.clicked {
  opacity: 0;
  visibility: hidden;
}

.wrapper {
  width: 95vw;
  height: 85vh;
  margin-bottom: 1vh;
  position: relative;
  left: 2vw;
  overflow: hidden;
  border-radius: 2.75rem;
}
.wrapper::before {
  content: "";
  position: absolute;
  width: 150%;
  height: 250%;
  top: -75%;
  left: -25%;
  transform-origin: center;
  background: conic-gradient(rgba(255, 255, 252, 0.02) 25%, var(--darker-blue) 50%, rgba(255, 255, 252, 0.02) 51%, rgba(255, 255, 252, 0.02) 75%, var(--darker-blue) 90%);
  animation: rotate 5s ease-in-out infinite;
  animation-fill-mode: forwards;
}
.wrapper .gameContainer {
  background-color: var(--white);
  background-image: linear-gradient(to bottom, var(--white) 45%, var(--light-blue) 95%);
  border-radius: 2.5rem;
  width: 99.5%;
  height: 99%;
  position: relative;
  left: 0.25%;
  top: 0.5%;
  padding: 0.75rem;
  display: grid;
  overflow: hidden;
  grid-gap: 0.5rem 0.5rem;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: repeat(6, 1fr);
}
.wrapper .gameContainer .category, .wrapper .gameContainer .clue {
  border: 3px solid var(--very-dark-blue);
  /* color: var(--dark-blue); */
  color: var(--main-blue);
  border-radius: 2.5%;
  box-shadow: -0.05rem 0.05em 0.5rem var(--darker-blue);
  font-weight: bold;
  text-shadow: -0.1rem 0.1rem 0.25rem var(--very-dark-blue);
  margin-bottom: 0;
  text-align: center;
  opacity: 0;
  animation: ease-in-out 3s forwards 1;
  transition: all 0.5s ease;
  /* height: 50%; */
  /* width: 50%; */
}
.wrapper .gameContainer .category {
  font-size: 2rem;
  background-color: var(--light-blue);
  color: var(--very-dark-blue);
}
.wrapper .gameContainer .clue {
  font-size: 3.5rem;
  transition: all 300ms;
}
.wrapper .gameContainer .clue:hover {
  box-shadow: -0.15rem 0.15em 0.5rem var(--darker-blue);
  border-color: var(--brown);
  background-color: var(--white);
  text-shadow: -0.1rem 0.1rem 0.25rem var(--dark-blue);
}

@keyframes rotate {
  100% {
    transform: rotate(360deg);
  }
}
@keyframes fadeInClues {
  from {
    transform: translateY(-5%);
    opacity: 0;
  }
  to {
    transfrom: translateY(0);
    opacity: 1;
  }
}
.header {
  width: 99vw;
  height: 7vh;
}
.header__title {
  color: var(--main-blue);
  width: auto;
  height: 100%;
  margin: 0 auto;
  font-family: var(--title-font);
  font-size: 4.25rem;
  line-height: auto;
  text-align: center;
  text-shadow: 0.25rem 0.2rem 0.25rem var(--black);
}

.footer {
  width: 100vw;
  height: 7vh;
  padding: 0 5.5rem 0 4.5rem;
  overflow: hidden;
  position: relative;
  display: flex;
  justify-content: space-between;
  font-family: var(--title-font);
  font-weight: var(--regular-font-weight);
}
.footer__score, .footer__timer {
  display: inline-block;
  color: var(--main-blue);
  width: 20%;
  height: 100%;
  font-size: 3rem;
  line-height: 3rem;
  padding: 0;
  text-shadow: -0.1rem 0.1rem 0.25rem var(--very-dark-blue);
  /* margin-top: 1vh; */
}
.footer__score.scoreNum, .footer__timer.scoreNum {
  color: var(--very-dark-blue);
}
.footer__timer {
  width: 10%;
  text-align: right;
}

.preview {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 2;
  justify-content: center;
  position: absolute;
  background-color: var(--white);
  transition: all 3s ease;
}
.preview__title {
  color: var(--dark-blue);
  width: 50%;
  height: auto;
  font-family: var(--title-font);
  font-size: 6rem;
  text-align: center;
  text-shadow: 0.25rem 0.2rem 0.25rem var(--black);
}
.preview__text {
  color: var(--darker-blue);
  width: 50%;
  height: 40%;
  font-family: var(--text-font);
  font-size: 3.5rem;
  text-align: center;
}
.preview__start {
  width: 20rem;
  height: 5rem;
  color: var(--darker-blue);
  border-radius: 0.75rem;
  font-weight: var(--bold-font-weight);
}
.preview__start:hover {
  background-color: #ced9f7;
}

.end {
  width: 100vw;
  height: 93vh;
  position: absolute;
  top: 7vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  opacity: 0;
  visibility: hidden;
  display: none;
  transition: all 3s ease;
  z-index: 100;
  background-color: var(--white);
  color: var(--main-blue);
  font-size: 4rem;
}
.end__score {
  width: 50%;
  height: 20%;
  display: block;
  text-align: center;
  text-shadow: 0.25rem 0.2rem 0.25rem var(--black);
}
.end__restart {
  width: 20rem;
  height: 5rem;
  color: var(--darker-blue);
  border-radius: 0.75rem;
  display: block;
  font-weight: var(--bold-font-weight);
}
.end__restart:hover {
  background-color: #ced9f7;
}

.question {
  position: absolute;
  width: 94.25vw;
  height: 84vh;
  top: 7.5vh;
  left: 2.35vw;
  z-index: 1;
  border-radius: 2.75rem;
  overflow: hidden;
  line-height: 3.5rem;
  padding: 1rem;
  background-color: var(--main-blue);
}
.question .ques, .question .ans {
  color: var(--very-dark-blue);
  font: var(--body-font);
  font-size: 3.5rem;
  margin: 0.5rem 0;
}
.question .ans {
  /* border: 7px solid black; */
  margin-left: 0.5rem;
  width: 50%;
}